.sPost-top-bg {
  background: url(../../img/279563.png);
  height: 350px;
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 0;
  
  .sPost-top {
    display: flex;
    justify-content: center;
  
    h1 {
      @include font(50px, $font-regular, $font-color-primary-1);
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
    }
  }
}

.single-post-area{
  margin-bottom: 50px;
  margin-top: 50px;
    
  .posts-list{
    padding-left: 0px;


    .single-post{
      display: flex;
      flex-wrap: wrap;
      padding: 30px;
      margin: 0px;
      
      border: 1px solid #eee;


      h3{
        display: flex;
        justify-content: center;
        margin-bottom: 25px;
      }

      .feature-img{
        padding-top: 5px;
        padding-bottom: 5px;
        margin-bottom: 30px;
      }
    }
  
    .meta-details{
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      text-align: right;
    }
  }

}


.comments-area{
  background: #fafaff;
  margin-top: 50px;
  margin-bottom: 50px;
  border: 1px solid #eee;
  padding: 32px 20px;
  padding-bottom: 0px;
   
  h3{
    font-family: $font-semiBold;
    font-size: 24px;
    color: #222;
    text-align: center;
    margin-bottom: 23px;
  }

  .single-comment {
    margin-bottom: 20px;
    border-top: 1px solid #eee;
    padding-top: 30px;

    img{
      width: 80px;
      margin-right: 15px;
      
    }

    a{
      text-decoration: none;
      outline: 0;
      font-size:16px;
      color: #222;
    }

    p {
      margin-top: 0;
      margin-bottom: 1rem;
      display: block;
      font-size:14px;
      width: 107%;
    }

    .reply-btn{
      width: 6%;
      height: 100%;
    }
  }
}





.sPost{
  padding-left: 0px;
  padding-right: 0px;
}

.sPost-body-main {
  margin-top: 50px;
  margin-bottom: 50px;
  border: 1px solid #eee;

 
}

.sPost-body-comment{
  margin: 0;
  display: flex;
  justify-content: center;

  .comment-form{
    h4{
      @include font(24px, $font-regular, $font-color-primary-3);

    }
    
    display: flex;
    justify-content: center;
    margin: 10px;
    padding: 5px;
  }

}

.form-group-sPost{
  margin: 0;
}


.sPost-input {
  @include font(15px, $font-regular, $font-color-primary-2);
  margin-bottom: 30px;
}

div .sPost-textarea-field {
  @include font(15px, $font-regular, $font-color-primary-2);
  height: 203px;
}

.sPost-btn{
    margin-top: 0;
    margin-bottom: 20px;
}

.sPost-btn {
  &:hover {
    text-decoration: none;
  }
}

.sPost-send-btn {
  padding: 0;
  
  display: flex;
  justify-content: center;
  margin-left: 289px;
}

.text-comment {
  padding: 0;
}








